import React from 'react';
import './Job.css';

const Job = ({ start, end, company, website, title, stack, description }) => (
  <div className="Job">
    <h3>{start} - {end ? end : 'Present'}</h3>
    <div className="Job-company">
      {company && website && <p className="Job-company-name">{company} - {website}</p>}
      <p className="Job-company-title">{title}</p>
    </div>
    <p className="Job-stack">Technologies: {stack}</p>
    <p className="Job-description">{description}</p>
  </div>
);

export default Job;