import React, {
  Component
} from 'react';
import Section from '../components/Section';
import Job from '../components/Job';
import data from './career';

class Career extends Component {
  render() {
    return (
      <Section hr={true} title="Career">
        {data.list.reverse().map((job) => (
          <Job {...job} />
        ))}
      </Section>
    );
  }
}

export default Career;